import React, { useEffect, useMemo, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import DatePicker from 'react-datepicker'
import dayjs from 'dayjs'
import { ko } from 'date-fns/esm/locale'
import styled from 'styled-components'
import _ from 'lodash'

// react-bootstrap components
import { Row, Col } from 'react-bootstrap'
import TableBox from 'components/Box/TableBox'
import {
	getAccountBookList,
	updateAccountBook,
	deleteAccountBook,
} from 'store/reducer/accountBook'

import 'react-date-range/dist/styles.css'
import 'react-date-range/dist/theme/default.css'
import 'react-datepicker/dist/react-datepicker.css'
import { setComma } from 'util/common'
import { modalprops } from 'components/Modal/Modal'

// style
const CustomDatePicker = styled(DatePicker)`
	width: 190px;
`

const CenterCol = styled(Col)`
	float: none;
	margin: 0 auto;
`

// component
function AccountBookDetail() {
	const [startDate, setStartDate] = useState(dayjs().subtract(7, 'day').toDate())
	const [endDate, setEndDate] = useState(dayjs().toDate())

	// Redux Init
	const accountInfo = useSelector(state => state.accountBook)
	// computed 
	const { fixedList, notFixedList } = useSelector(state => {
		const list = _.cloneDeep(state.accountBook.accountList)
		return list.reduce(
			(acc, cur) => {
				const curData = _.cloneDeep(cur)
				curData.date = dayjs(curData.date).format('YYYY-MM-DD')
				if (curData.isFixed) {
					acc.fixedList.push(curData)
				} else {
					acc.notFixedList.push(curData)
				}
				return acc
			},
			{ fixedList: [], notFixedList: [] },
		)
	})
	const userInfo = useSelector(state => state.user)
	const dispatch = useDispatch()

	useEffect(() => {
		notFixedModalProps.buttons.submit.callback = async data => {
			const res = await dispatch(
				updateAccountBook({ ...data, userId: 'gun4930' }),
			).unwrap()
			return !!res.code
		}
	}, [])

	useEffect(() => {
		dispatch(
			getAccountBookList({
				userId: userInfo.userId,
				startDate: dayjs(startDate).format('YYYY-MM-DD'),
				endDate: dayjs(endDate).format('YYYY-MM-DD'),
			}),
		)
	}, [startDate, endDate])

	const defaultColumns = [
		{
			dataField: 'accountId',
			text: 'No',
			headerClasses: 'border-0',
			headerStyle: { width: '50px' },
		},
		{
			dataField: 'content',
			text: '',
			headerClasses: 'border-0',
		},
		{
			dataField: 'amount',
			text: '',
			headerClasses: 'border-0',
			formatter: cell => {
				return <span>{`${setComma(cell)}`}</span>
			},
		},
		{
			dataField: 'category',
			text: '',
			headerClasses: 'border-0',
		},
	]

	const notFixedColumns = _.cloneDeep(defaultColumns)
	notFixedColumns.push({
		dataField: 'date',
		text: '',
		headerClasses: 'border-0',
	})

	const fixedColumns = _.cloneDeep(defaultColumns)
	fixedColumns.push(
		{
			dataField: 'date',
			text: '',
			headerClasses: 'border-0',
		},
		{
			dataField: 'fixedDuration',
			text: '',
			headerClasses: 'border-0',
		},
	)

	const onClickTableUpdate = accountInfo => {
		const result = dispatch(updateAccountBook(accountInfo))
		return result
	}

	const onClickTableDelete = async accountId => {
		const result = dispatch(deleteAccountBook({ userId: userInfo.userId, accountId }))
		return result
	}

	return (
		<>
			<Row>
				<CenterCol md="3">
					<CustomDatePicker
						dateFormat="yyyy-MM-dd"
						locale={ko}
						selected={startDate}
						startDate={startDate}
						endDate={endDate}
						onChange={update => {
							setStartDate(update)
						}}
						selectsStart
					/>
					<CustomDatePicker
						dateFormat="yyyy-MM-dd"
						locale={ko}
						selected={endDate}
						startDate={startDate}
						endDate={endDate}
						minDate={startDate}
						onChange={update => {
							setEndDate(update)
						}}
						selectsEnd
					/>
				</CenterCol>
			</Row>
			<Row>
				<Col>
					<TableBox
						columnId="accountId"
						title=" /"
						description="      !"
						tableData={notFixedList}
						columns={notFixedColumns}
						onClickUpdate={onClickTableUpdate}
						onClickDelete={onClickTableDelete}
						modalProps={notFixedModalProps}
					></TableBox>
				</Col>
			</Row>
			<Row>
				<Col>
					<TableBox
						columnId="accountId"
						title=" /"
						description="      !"
						tableData={fixedList}
						columns={fixedColumns}
						onClickUpdate={onClickTableUpdate}
						onClickDelete={onClickTableDelete}
						modalProps={modalprops}
					></TableBox>
				</Col>
			</Row>
		</>
	)
}

const notFixedModalProps = {
	title: '  ',
	fields: [
		{
			label: '',
			placeholder: '',
			value: '',
			type: 'text',
			name: 'content',
		},
		{
			label: '',
			placeholder: '',
			value: '',
			type: 'text',
			name: 'amount',
			required: true,
			pattern: /^[-]?[0-9]+$/,
			errormessage: '  .',
		},
		{
			label: '',
			placeholder: '',
			value: '',
			type: 'text',
			name: 'category',
		},
		{
			label: '',
			placeholder: '',
			value: '',
			type: 'date',
			name: 'date',
		},
	],
	buttons: {
		customs: [{ text: '', handleClick: () => {} }],
		submit: {
			use: true,
			text: '',
		},
		reset: {
			use: true,
			text: '',
		},
	},
}
export default AccountBookDetail
