import React, { useEffect, useState } from 'react'
import ChartistGraph from 'react-chartist'
import { useDispatch, useSelector } from 'react-redux'
import dayjs from 'dayjs'

// react-bootstrap components
import { Card, Dropdown, Nav, Container, Row, Col } from 'react-bootstrap'
import SummaryMiniBox from 'components/Box/SummaryMiniBox'
import { calcSummary, getAccountBookList } from 'store/reducer/accountBook'
import { setComma } from 'util/common'

function AccountBook() {
	const userInfo = useSelector(state => state.user)
	const dispatch = useDispatch()
	const [durationType, setDurationType] = useState('d')
	const [isFixedIncome, setIsFixedIncome] = useState(true)
	const [isFixedOutcome, setIsFixedOutcome] = useState(true)

	const durationInfo = {
		// FIXME    cnt  
		d: {
			cnt: 30,
			name: '',
		},
		w: {
			cnt: 12 * 7,
			name: '',
		},
		m: {
			cnt: 12 * 30,
			name: '',
		},
		y: {
			cnt: 3,
			name: '',
		},
	}
	useEffect(() => {
		const startDate = dayjs()
			.subtract(durationInfo[durationType].cnt, durationType)
			.format('YYYY-MM-DD')
		dispatch(
			getAccountBookList({
				userId: userInfo.userId,
				startDate,
				endDate: dayjs().format('YYYY-MM-DD'),
			}),
		)
	}, [])

	const { fixedOutcome, fixedIncome, notFixedIncome, notFixedOutcome } = useSelector(
		state => {
			const firstDay = dayjs().set('date', 1)
			let list = _.cloneDeep(state.accountBook.accountList)
			list = list.filter(account => {
				return (
					account.isFixed ||
					(dayjs(account.date).diff(firstDay) >= 0 && dayjs().diff(account.date) >= 0)
				)
			})
			return calcSummary(list, {
				startDate: firstDay.format('YYYY-MM-DD'),
				endDate: dayjs().format('YYYY-MM-DD'),
			})
		},
	)

	const accountSum =
		(isFixedIncome ? fixedIncome + notFixedIncome : notFixedIncome) +
		(isFixedOutcome ? fixedOutcome + notFixedOutcome : notFixedOutcome)
	const summaryBoxOptionList = [
		{
			title: '',
			value: `${setComma(
				isFixedIncome ? fixedIncome + notFixedIncome : notFixedIncome,
			)}`,
			mainIconOption: {
				type: 'icon',
				class: 'fas fa-caret-up text-success',
			},
			subIconOption: {
				type: 'checkbox',
				name: ' ',
				value: true,
				onClick: () => {
					setIsFixedIncome(!isFixedIncome)
				},
			},
		},
		{
			title: '',
			value: `${setComma(
				isFixedOutcome ? fixedOutcome + notFixedOutcome : notFixedOutcome,
			)}`,
			mainIconOption: {
				type: 'icon',
				class: 'fas fa-caret-down text-danger',
			},
			subIconOption: {
				type: 'checkbox',
				name: ' ',
				value: true,
				onClick: e => {
					setIsFixedOutcome(e.target.checked)
				},
			},
		},
		{
			title: '',
			value: `${setComma(accountSum)}`,
			mainIconOption: {
				type: 'icon',
				class: 'fas fa-chart-pie text-warning',
			},
			subIconOption: {
				type: 'icon',
				name: '/ ',
				class: 'fas fa-check',
			},
		},
	]
	return (
		<>
			<Container fluid>
				<h4>/ ()</h4>
				<Row>
					{summaryBoxOptionList.map(option => {
						return (
							<Col lg="4" sm="6" key={option.title}>
								<SummaryMiniBox
									title={option.title}
									value={option.value}
									onBtnClick={option.onBtnClick}
									btnName={option.btnName}
									mainIconOption={option.mainIconOption}
									subIconOption={option.subIconOption}
								></SummaryMiniBox>
							</Col>
						)
					})}
				</Row>
				<Dropdown as={Nav.Item}>
					<Dropdown.Toggle
						as={Nav.Link}
						data-toggle="dropdown"
						id="dropdown-67443507"
						variant="default"
						className="m-0"
					>
						<i className="nc-icon nc-planet"></i>
						<span className="notification"></span>
						<span className="d-lg-none ml-1">Notification</span>
					</Dropdown.Toggle>
					<Dropdown.Menu>
						<Dropdown.Item href="#pablo" onClick={e => e.preventDefault()}>
							
						</Dropdown.Item>
						<Dropdown.Item href="#pablo" onClick={e => e.preventDefault()}>
							
						</Dropdown.Item>
						<Dropdown.Item href="#pablo" onClick={e => e.preventDefault()}>
							
						</Dropdown.Item>
					</Dropdown.Menu>
				</Dropdown>
				<Row>
					<Col md="8">
						<Card>
							<Card.Header>
								<Card.Title as="h4">Users Behavior</Card.Title>
								<p className="card-category">24 Hours performance</p>
							</Card.Header>
							<Card.Body>
								<div className="ct-chart" id="chartHours">
									<ChartistGraph
										data={{
											labels: [
												'9:00AM',
												'12:00AM',
												'3:00PM',
												'6:00PM',
												'9:00PM',
												'12:00PM',
												'3:00AM',
												'6:00AM',
											],
											series: [
												[287, 385, 490, 492, 554, 586, 698, 695],
												[67, 152, 143, 240, 287, 335, 435, 437],
												[23, 113, 67, 108, 190, 239, 307, 308],
											],
										}}
										type="Line"
										options={{
											low: 0,
											high: 800,
											showArea: false,
											height: '245px',
											axisX: {
												showGrid: false,
											},
											lineSmooth: true,
											showLine: true,
											showPoint: true,
											fullWidth: true,
											chartPadding: {
												right: 50,
											},
										}}
										responsiveOptions={[
											[
												'screen and (max-width: 640px)',
												{
													axisX: {
														labelInterpolationFnc: function (value) {
															return value[0]
														},
													},
												},
											],
										]}
									/>
								</div>
							</Card.Body>
							<Card.Footer>
								<div className="legend">
									<i className="fas fa-circle text-info"></i>
									Open <i className="fas fa-circle text-danger"></i>
									Click <i className="fas fa-circle text-warning"></i>
									Click Second Time
								</div>
								<hr></hr>
								<div className="stats">
									<i className="fas fa-history"></i>
									Updated 3 minutes ago
								</div>
							</Card.Footer>
						</Card>
					</Col>
					<Col md="4">
						<Card>
							<Card.Header>
								<Card.Title as="h4">Email Statistics</Card.Title>
								<p className="card-category">Last Campaign Performance</p>
							</Card.Header>
							<Card.Body>
								<div className="ct-chart ct-perfect-fourth" id="chartPreferences">
									<ChartistGraph
										data={{
											labels: ['40%', '20%', '40%'],
											series: [40, 20, 40],
										}}
										type="Pie"
									/>
								</div>
								<div className="legend">
									<i className="fas fa-circle text-info"></i>
									Open <i className="fas fa-circle text-danger"></i>
									Bounce <i className="fas fa-circle text-warning"></i>
									Unsubscribe
								</div>
								<hr></hr>
								<div className="stats">
									<i className="far fa-clock"></i>
									Campaign sent 2 days ago
								</div>
							</Card.Body>
						</Card>
					</Col>
				</Row>
			</Container>
		</>
	)
}

export default AccountBook
